import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function NiobrarasaurusColeii() {
    return (
        <div class="container">
            <Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/Fossil%20(3).jpeg" alt="Niobrarasaurus Coleii" width="125"></img></div>
                <div class="col-sm"><h1>Niobrarasaurus Coleii</h1></div>
            </div>
            <p>Niobrarasaurus were herbivores who walked on all fours and were protected by a layer of armor made out of hard plates.</p>

            <div>
            <img class="img-responsive" src="http://oceansofkansas.com/Niobrarasaurus/BeachedNioa.jpg" alt="Niobrarasaurus Coleii" height="200"></img>
            </div>
        </div>
    );
}

export default NiobrarasaurusColeii;