import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function PteranodonSternbergi() {
    return (
        <div class="container">
            <Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/Fossil%20(1).jpeg" alt="Pteranodon Sternbergi" width="125"></img></div>
                <div class="col-sm"><h1>Pteranodon Sternbergi</h1></div>
            </div>
            <p>Pteranodon Sternbergi was a large flying reptile with a wingspan over 7 meters (23 feet). They lived during the late Cretaceous geological period of North America in present-day Kansas, Alabama, Nebraska, Wyoming, and South Dakota.</p>
            
            <div>
            <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/7/77/Pteranodon_amnh_martyniuk.jpg" alt="Pteranodon Sternbergi" height="200"></img>
            <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/c/cf/Pteranodon_sternbergi_pair.jpg" alt="Pteranodon Sternbergi" height="200"></img>
            </div>

        </div>
    );
}

export default PteranodonSternbergi;