import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function TriceratopsHorridus() {
    return (
        <div class="container">
            <Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/fossil2%20(3).jpeg" alt="Triceratops Horridus" width="125"></img></div>
                <div class="col-sm"><h1>Triceratops Horridus</h1></div>
            </div>
            <p>The Triceratops was a herbivore that first appeared during the late Maastrichtian stage of the Late Cretaceous period, about 68 million years ago (mya) in what is now North America.</p>


            <div>
            <img class="img-responsive" src="https://i.natgeofe.com/n/b96b572c-98e2-4ec2-a714-08a6b95cf646/triceratopshorridus_hexdcb.jpg" alt="Niobrarasaurus Coleii" height="200"></img>
            <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/ec/LA-Triceratops_mount-2.jpg/1200px-LA-Triceratops_mount-2.jpg" alt="Niobrarasaurus Coleii" height="200"></img>
            </div>
        </div>
    );
}

export default TriceratopsHorridus;