import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function Archaeotherium() {
    return (
        <div class="container">
            <Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/fossil2%20(2).jpeg" alt="Archaeotherium" width="125"></img></div>
                <div class="col-sm"><h1>Archaeotherium</h1></div>
            </div>
            <p>Archaeotherium lived during the Eocene and Oligocene epochs (33.9—24.8 mya). They were omnivore's who lived primarily in modern day Canada and USA.</p>

        <div>
        <img class="img-responsive" src="https://static.wikia.nocookie.net/prehistoric-life/images/4/40/Archaeotherium.png" alt="Archaeotherium" height="200"></img>
        <img class="img-responsive" src="https://i.pinimg.com/originals/8a/53/cc/8a53cc86f9de924d182a98a916cbc78d.jpg" alt="Archaeotherium" height="200"></img>
        </div>

        </div>
    );
}

export default Archaeotherium;