import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function DolichorhynchopsOsborni() {
    return (
        <div class="container">
            <Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/Fossil%20(2).jpeg" alt="Dolichorhynchops Osborni" width="125"></img></div>
                <div class="col-sm"><h1>Dolichorhynchops Osborni</h1></div>
            </div>
            <p>Dolichorhynchops Osborni lived during the Turonian to Campanian of the Cretaceous period. </p>

            <div>
            <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/b/b8/Dolichorhynchops.jpg" alt="Dolichorhynchops Osborni" height="200"></img>
            <img class="img-responsive" src="http://images.dinosaurpictures.org/Dolichorhynchops%2B(2)_fe26.jpg" alt="Dolichorhynchops Osborni" height="200"></img>
            </div>
        </div>

    );
}

export default DolichorhynchopsOsborni;