import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function Mammoth() {
    return (
        <div class="container">
        	<Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/image1.jpeg" alt="Mammoth" width="125"></img></div>
                <div class="col-sm"><h1>Mammoth</h1></div>
            </div>
            <p>They lived from the Pliocene epoch (around 5 million years ago) to the Holocene epoch at about 4,000 years ago, and various species existed in Africa, Europe, Asia, and North America.</p>
            <div>
            	<img class="img-responsive" src="https://cdn.britannica.com/09/74609-050-21E14E52/example-museum-replica-species-Canadian-de-extinction.jpg" alt="Dolichorhynchops Osborni" height="200"></img>
            	<img class="img-responsive" src="https://images.newscientist.com/wp-content/uploads/2016/08/01165905/c0205688-woolly_mammoth_artwork-spl.jpg?width=600" alt="Dolichorhynchops Osborni" height="200"></img>
            </div>
        </div>
    );
}

export default Mammoth;