import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function SmilodonFatalis() {
    return (
        <div class="container">
            <Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/image0.jpeg" alt="Smilodon Fatalis" width="125"></img></div>
                <div class="col-sm"><h1>Smilodon Fatalis</h1></div>
            </div>
            <p>Also known as the saber-toothed tiger, the Smilodon Fatalis lived during the Pleistocene epoch (2.5 mya – 10,000 years ago).</p>
            <div>
            <img class="img-responsive" src="https://i.imgur.com/kEnwjeK.jpg" alt="Niobrarasaurus Coleii" height="200"></img>
            <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/7/78/Smilodon_californicus_mount.jpg" alt="Niobrarasaurus Coleii" height="200"></img>
            </div>
        </div>
    );
}

export default SmilodonFatalis;