import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";

function Merycochoerus() {
    return (
        <div class="container">
        	<Link to="/Wildlife">
            <button type="button" class="btn btn-dark">Back</button>
            </Link>
            <div class="row">
                <div class="col-sm-auto"><img class="img-responsive" src="https://github.com/brettbookbinder/gathertown/raw/master/fossil2%20(4).jpeg" alt="Merycochoerus" width="125"></img></div>
                <div class="col-sm"><h1>Merycochoerus</h1></div>
            </div>
            <p>Merycochoerus were amphibious herbivores who lived during the Early Oligocene 33.9—30.8 mya, existing for approximately 3 million years.</p>

        <div>
        <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c1/Promerycochoerus.jpg/330px-Promerycochoerus.jpg" alt="Archaeotherium" height="200"></img>
        <img class="img-responsive" src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/21/Merycochoerus_proprius.jpg/330px-Merycochoerus_proprius.jpg" alt="Archaeotherium" height="200"></img>
        </div>
        </div>
    );
}

export default Merycochoerus;