import { HashRouter, Switch, Route } from 'react-router-dom';
import Main from "./components/Main";

import PteranodonSternbergi from "./components/PteranodonSternbergi";
import DolichorhynchopsOsborni from "./components/DolichorhynchopsOsborni";
import NiobrarasaurusColeii from "./components/NiobrarasaurusColeii";
import TylosaurusProriger from "./components/TylosaurusProriger";
import FishWithinAFish from "./components/FishWithinAFish";
import Archaeotherium from "./components/Archaeotherium";
import TriceratopsHorridus from "./components/TriceratopsHorridus";
import Merycochoerus from "./components/Merycochoerus";
import CorythosaurusCasuarius from "./components/CorythosaurusCasuarius";
import Mammoth from "./components/Mammoth";
import SmilodonFatalis from "./components/SmilodonFatalis";
import Frog from "./components/Frog";

function App() {
  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={Main}/>
        <Route path="/wildlife" component={Main}/>
        <Route path="/fossils" component={Main}/>
        <Route path="/PteranodonSternbergi" component={PteranodonSternbergi}/>\
        <Route path="/DolichorhynchopsOsborni" component={DolichorhynchopsOsborni}/>
        <Route path="/NiobrarasaurusColeii" component={NiobrarasaurusColeii}/>
        <Route path="/TylosaurusProriger" component={TylosaurusProriger}/>
        <Route path="/FishWithinAFish" component={FishWithinAFish}/>
        <Route path="/Archaeotherium" component={Archaeotherium}/>
        <Route path="/TriceratopsHorridus" component={TriceratopsHorridus}/>
        <Route path="/Merycochoerus" component={Merycochoerus}/>
        <Route path="/CorythosaurusCasuarius" component={CorythosaurusCasuarius}/>
        <Route path="/Mammoth" component={Mammoth}/>
        <Route path="/SmilodonFatalis" component={SmilodonFatalis}/>
        <Route path="/Frog" component={Frog}/>

      </Switch>
    </HashRouter>
  );
}

export default App;